import React, { useEffect, useState, useRef } from "react";
// import { Grid, CssBaseline, Paper, Hidden } from "@material-ui/core";
import Logo from "../../assets/image/Logo.png";
import { Link } from "react-router-dom";
import { useCookies } from "react-cookie";
import { Button, Col, Row } from "react-bootstrap";
import "./home.style.scss";
import { Toast } from "./../../components/toaster/toaser";

const HomePage = ({ match: { url, path }, history: { push } }) => {
  const logInOrOutRef = useRef();
  const [width, setWidth] = useState(0);
  const [height, setHeight] = useState(100);
  const [detailsContainer, setDetailsContainer] = useState({});
  const [screenAndFrame, setScreenAndFrame] = useState({
    screen: {
      width: 0,
      height: 0,
    },
    difference: {
      x: 0,
      y: 0,
    },
  });

  useEffect(() => {
    setScreenAndFrame({
      screen: { width: window.innerWidth, height: window.innerHeight },
      difference: { x: 0, y: 0 },
    });
    const reportWindowSize = (e) => {
      const loginContainer = document.querySelector(".login-container");
      setScreenAndFrame((oldVal) => ({
        screen: {
          width: e.currentTarget.innerWidth,
          height: e.currentTarget.innerHeight,
        },
        difference: {
          x: oldVal.difference.x + oldVal.screen.width - window.innerWidth,
          y: oldVal.difference.y + oldVal.screen.height - window.innerHeight,
        },
      }));
    };
    window.addEventListener("resize", reportWindowSize);
    setWidth(window.innerWidth - 50);
    setHeight(window.innerHeight - 50);

    setTimeout(() => {
      logInOrOutRef.current.style.top = `${
        50 - logInOrOutRef.current.getBoundingClientRect().height / 2
      }px`;
    }, 1);
  }, []);

  const [cookies, setCookie, removeCookie] = useCookies(["userdata"]);
  return (
    <div className="home">
      <Row noGutters xs={12} sm={12} md={12}>
        <Col sm={12} className="bgLines" />
        <Col sm={7} className="ahmedsDetails">
          <div style={{ left: "100vw" }} className="ahmeds-details-container">
            <h2 className="association">مرشح جمعية أشبيلية التعاونية</h2>
            <div className="ahmeds-details">
              <h1 className="ahmeds-name">
                أحمد <span className="elasfar-name">الأصفر</span>
              </h1>
            </div>
            <h1 className="_2021">2021</h1>
            <Button
              className="show-users-button"
              color="white"
              onClick={() => {
                push(cookies["userdata"] ? "/table" : "/account/signin");
                if (!cookies["userdata"])
                  Toast(
                    "الرجاء تسجيل الدخول اولأ لتتمكن من مشاهدة الأعضاء",
                    true
                  );
              }}
            >
              عرض المساهمين
            </Button>
            {cookies["userdata"]?.name === "admin" && (
              <Button
                className="show-users-button"
                color="white"
                onClick={() => push("/users-table")}
              >
                عرض الأعضاء
              </Button>
            )}
          </div>
        </Col>
        {/* <Hidden only={["xs"]}> */}
        {/* <Col> */}
        <div className="home-rectangle">
          <svg
            version="1.1"
            id="Layer_1"
            x="0px"
            y="0px"
            className="outline-bg"
          >
            <path
              id="inner_line"
              fill="none"
              stroke="#fff"
              stroke-miterlimit="10"
              d={`
            M${width - screenAndFrame.difference.x - 418}
            ,${height - screenAndFrame.difference.y}

            L50,${height - screenAndFrame.difference.y}

            L50,50
            L${width - screenAndFrame.difference.x - 175},50

            M${width - screenAndFrame.difference.x - 40},50

            L${width - screenAndFrame.difference.x},50

            L${width - screenAndFrame.difference.x}
            ,${height - screenAndFrame.difference.y}

            L${width - screenAndFrame.difference.x - 40}
            ,${height - screenAndFrame.difference.y}
          `}
            />
          </svg>
        </div>
        <span
          className="signOut"
          style={
            width > 500
              ? {
                  left: width - screenAndFrame.difference.x - 166,
                }
              : null
          }
          ref={logInOrOutRef}
          onClick={() => {
            if (cookies["userdata"]) removeCookie("userdata");
            else push(`${url}account/signin`);
          }}
        >
          {cookies["userdata"] ? "تسجيل الخروج" : "تسجيل الدخول"}
        </span>
        {/* </Col> */}
        {/* </Hidden> */}
        <Col sm={12} className="ahmedsPhoto" />
        <img src={Logo} alt="Logo" className="home-logo" />
      </Row>
    </div>
  );
};

export default HomePage;
